.container-register {
  z-index: 12;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  box-sizing: border-box;
  padding: 20px 0 20px;
  width: max-content;
  display: inline-block;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -200px -255px;
}

.input-container {
  text-align: center;
}

.register-input {
  display: block;
  margin: 30px 10px 10px 30px;
  padding: 14px 16px;
  border: 1px solid #dddfe2;
  border-radius: 6px;
  width: 332px;
  font-size: 17px;
}

.register-email {
  display: flex;
  margin-top: -12px;
}

.btn-validate {
  cursor: pointer;
  background-color: var(--color-primary);
  color: rgb(255, 255, 255);
  border: none;
  border-radius: 6px;
  padding: 10px;
  width: 96px;
  height: 40px;
  text-align: center;
  margin: 34px 10px 0;
}

.btn-validate:hover {
  background-color: var(--color-hover);
}

.btn-register {
  word-wrap: break-word;
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
  white-space: nowrap;
  background-color: var(--color-primary);
  color: rgb(255, 255, 255);
  border: none;
  border-radius: 6px;
  font-size: 20px;
  line-height: 48px;
  padding: 0 16px;
  width: 250px;
  margin: 40px 30px 40px 30px;
}

.btn-register:hover {
  background-color: var(--color-hover);
}

.register {
  margin-bottom: 30px;
}
