.btn-logout-nav {
    border: none;
    background: transparent;
    padding: 6px 16px;
    font-size: 15px;
    color: var(--color-gray);
    background: white;
    border-radius: 5px;
    transition: 0.3s all ease-out;
    cursor: pointer;
  }
  
  .btn-logout-nav:hover {
    color: var(--color-primary);
  }