.btn-mypage-nav {
    border: none;
    background: transparent;
    padding: 6px 16px;
    font-size: 15px;
    color: var(--color-gray);
    border-radius: 5px;
    transition: 0.3s all ease-out;
    cursor: pointer;

  }
  
  .btn-mypage-nav:hover {
    color: var(--color-primary);
  }