.container-modify {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  box-sizing: border-box;
  padding: 20px 40px 40px;
  width: max-content;
  height: max-content;
  display: inline-block;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 60%;
  margin: -320px -500px;
}

.user-info {
  font-size: 14px;
  margin-top: 100px;
}

.post-hit {
  text-align: right;
}
.post-date {
  text-align: right;
}

.chat-title {
  font-size: 50px;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 10%;
  transform: translate(-50%, -50%);
}

.chat-content {
  font-size: 20px;
  text-align: left;
  position: absolute;
  top: 30%;
}

.read-btns {
  position: absolute;
  top: 85%;
  left: 75%;
}

.btn-modify-check {
  word-wrap: break-word;
  cursor: pointer;
  background-color: var(--color-primary);
  color: rgb(255, 255, 255);
  border: none;
  border-radius: 6px;
  font-size: 16px;
  line-height: 40px;
  width: 150px;
  height: 40px;
  margin-left: 760px;
}

.btn-modify-check:hover {
  background-color: var(--color-hover);
}

.btn-delete {
  word-wrap: break-word;
  cursor: pointer;
  color: black;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  line-height: 40px;
  width: 150px;
  height: 40px;
  margin-right: 10px;
}

.btn-delete:hover {
  background-color: var(--color-hover);
}
